import React from 'react'
import { WeatherBlock } from "./WeatherBlock";
export const WeatherBox = ({ weatherReport, n }) => {
    let value = 8 * n - new Date(weatherReport.list[0].dt_txt).getHours() / 3;
    function dateBuilder(d) {
        let weekDay = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
        let months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
        return `${weekDay[d.getDay()]}, ${d.getDate()} ${months[d.getMonth()]} ${d.getFullYear()}`
    }
    function getWeather() {
        const list = document.getElementsByClassName('dropdown-list')[n];
        list.classList.toggle('show');
        document.getElementsByClassName('down-arrow')[n].classList.toggle('rotate');
        document.getElementsByClassName('weather-box')[n].classList.toggle('hover');
    }
    function getItems() {
        let k = 8 - new Date(weatherReport.list[0].dt_txt).getHours() / 3;
        const items = [];
        for (let i = 0; i <= k; i++)
            items.push(i);
        return items;
    }
    if (n === 0) return (
        <div className="weather-box hover" onClick={e => getWeather()}>
            <p id="dropdown-btn">Today
                <svg className="down-arrow" width="2rem" height="2rem" viewBox="0 0 64 44" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M3 3L32 38.5L61 3" id="path" stroke="#A6B1E1" strokeWidth="6" strokeLinecap="round" />
                </svg>
            </p>
            <ul className="dropdown-list">
                {getItems().map(k => (
                    <WeatherBlock key={k} weather={weatherReport.list[k]} />
                ))}
            </ul>
        </div>
    )
    else
        return (
            <div className="weather-box hover" onClick={e => getWeather()}>
                <p id="dropdown-btn">{dateBuilder(new Date(weatherReport.list[value].dt_txt))}
                    <svg className="down-arrow" width="2rem" height="2rem" viewBox="0 0 64 44" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M3 3L32 38.5L61 3" id="path" stroke="#A6B1E1" strokeWidth="6" strokeLinecap="round" />
                    </svg>
                </p>
                <ul className="dropdown-list">
                    <WeatherBlock weather={weatherReport.list[value]} />
                    <WeatherBlock weather={weatherReport.list[value + 1]} />
                    <WeatherBlock weather={weatherReport.list[value + 2]} />
                    <WeatherBlock weather={weatherReport.list[value + 3]} />
                    <WeatherBlock weather={weatherReport.list[value + 4]} />
                    <WeatherBlock weather={weatherReport.list[value + 5]} />
                    <WeatherBlock weather={weatherReport.list[value + 6]} />
                    <WeatherBlock weather={weatherReport.list[value + 7]} />
                    <WeatherBlock weather={weatherReport.list[value + 8]} />
                </ul>
            </div>
        )
}
