import React from 'react'

export const WeatherBlock = ({ k, weather }) => {
    let iconUrl = `http://openweathermap.org/img/wn/${weather.weather[0].icon}.png`;
    return (
        <>
            <hr />
            <li><span className="weather-time">{new Date(weather.dt_txt).toLocaleString('en-US', { hour: 'numeric', minute: 'numeric', hour12: true })}</span>
                <span className="weather-temp">{Math.round(weather.main.temp)}°C</span>
                <img src={iconUrl} alt="" />
                <span className="weather-desc">{weather.weather[0].description}</span>
            </li>
        </>
    )
}
