import React, { useState, useRef, useEffect } from "react";
import { WeatherBox } from "./WeatherBox";
function App() {
  const inputRef = useRef();
  const [weatherReport, setWeatherReport] = useState({});
  useEffect(() => {
    if (localStorage.getItem("city") === null || localStorage.getItem("city") === undefined)
      localStorage.setItem("city", "Hyderabad");
    fetch(getUrl(localStorage.getItem("city")))
      .then(res => res.json())
      .then(result => {
        setWeatherReport(result);
      })
  }, []);
  const getUrl = (city) => {
    return `https://api.openweathermap.org/data/2.5/forecast?q=${city}&units=metric&appid=${process.env.REACT_APP_API_KEY}`;
  }
  const search = (city) => {
    if (city === '') {
      alert("Please Enter a city name");
      return;
    }
    fetch(getUrl(city))
      .then(res => res.json())
      .then(result => {
        if (result.list === undefined) {
          alert("Please Enter a valid city name")
          inputRef.current.focus();
          return;
        }
        setWeatherReport(result);
        localStorage.setItem("city", city);
        inputRef.current.value = '';
        inputRef.current.blur();
      })
  }
  function dateBuilder(d) {
    let strTime = d.toLocaleString('en-US', { hour: 'numeric', minute: 'numeric', hour12: true });
    let weekDay = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
    let months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
    return `${strTime}, ${weekDay[d.getDay()]}, ${d.getDate()} ${months[d.getMonth()]} ${d.getFullYear()}.`
  }
  return (
    <div className="container">
      <h1 className="main-title">REACT 5-DAY WEATHER APP</h1>
      <div className="search-area">
        <input type="text" className="search-box" ref={inputRef} autoFocus placeholder="Search Location" onKeyPress={e => { if (e.key === 'Enter') search(inputRef.current.value) }} />
        <button className="search-btn" onClick={() => { search(inputRef.current.value) }}><i className="fas fa-search"></i></button>
      </div>
      {(typeof weatherReport.list != 'undefined') ?
        (
          <div className="weather-content">
            <h1 className="location">{weatherReport.city.name},{weatherReport.city.country}</h1>
            <p className="time">{dateBuilder(new Date())}</p>
            <div className="current-weather">
              <img className="mx-auto d-block" src={`http://openweathermap.org/img/wn/${weatherReport.list[0].weather[0].icon}.png`} alt="icon" />
              <p><span>{Math.round(weatherReport.list[0].main.temp)}°C</span><br />{Math.round(weatherReport.list[0].main.temp_min * 10) / 10}°C / {Math.round(weatherReport.list[0].main.temp_max * 10) / 10}°C <br />{weatherReport.list[0].weather[0].description}<br />
                Humidity: {weatherReport.list[0].main.humidity}%</p>
            </div>
            <div className="weather-group">
              < WeatherBox weatherReport={weatherReport} n={0} />
              < WeatherBox weatherReport={weatherReport} n={1} />
              < WeatherBox weatherReport={weatherReport} n={2} />
              < WeatherBox weatherReport={weatherReport} n={3} />
              < WeatherBox weatherReport={weatherReport} n={4} />
            </div>
          </div>
        )
        :
        (
          <div className="loader-container">
            <div className="loader"></div>
          </div>
        )}
      <p className="last-note">Made with ❤️ by T J SWAROOP</p>
    </div>
  );
}
export default App;
